 .sub-container {
    justify-content:space-between;
    width:max-content;
    left: 0;
    right:0

}
.nav-menu2{
    max-width: max-content;
    display: flex;
    float: left;
    flex-direction: column;
    align-items:flex-start;
    margin: 0;
    padding-left:0;
    padding-right: 3rem;
    padding-top: 90px;
}
.nav{
  writing-mode:sideways-lr;
    padding: 1rem;
    font-weight: 500px;
    font-size: 1.5rem;
    color: rgb(255, 250, 250);
    margin: 0;
    left: 0;
    
    cursor: pointer;
    text-shadow: 2px 2px 10px rgba(0,0,0, 0.5); 
}
.navActive {
    color:#de7e42;
    cursor: default;
    text-shadow: 3px 3px 15px rgba(255,255,255, .5);
  }
.navsub{
    font-size: 1.5rem;
    color: rgb(255, 250, 250);
    left:0;
}


